import fly from '../common/flyioRequest'
import qs from 'qs'
import URL from '../common/flyioRequest'

export const baseURL = window.serverUrl || window.location.protocol + '//' + window.location.host

/* 会员卡支付 */
export function getMemberPay (wxOpenId, payAmount, couponNo='', storeId = '', operatorId = '', mchId = '', tradeOrderId='', payToken) {
  return fly.request( '/api/qrcode/memberPay', qs.stringify({
    wxOpenId,
    payAmount,
    couponNo,
    storeId,
    operatorId,
    mchId,
    tradeOrderId,
    payToken
  }),{
    method: 'post',
  })
}

/* 微信支付 */
