/* 获取商户信息 */
export function getMchInfo (payToken = '', mchId = '', storeId = '', operatorId = '') {
  return fly.request( '/api/qrcode/mchInfo', qs.stringify({
      payToken,
      mchId,
      storeId,
      operatorId
  }),{
    method: 'post',
  })
}

/* 获取会员信息 */
export function getMemberInfoAndCoupons (payToken, mchId, storeId, wxOpenId) {
	return fly.request({
		url : '/api/qrcode/memberInfoAndCoupons',
		method: 'post',
		params: {mchId, wxOpenId, storeId, payToken}
	})
}

/* 获取支付成功后的广告信息 */
export function getAdInfoByPaySuccess (tradeOrderId) {

  return fly.request({
    url : window.mchUrl + '/api/order_advert/get_advert',
    method: 'get',
    params: {
      showType : 1, //显示位置：1-移动端支付成功页, 2-刷脸设备支付成功页, 3-商家APP轮播图片, 4-刷脸设备投屏广告
      tradeOrderId: tradeOrderId
    }
  });
}



