<template>

  <div>
    <div class="keyboard-mask" ref="keyboardMask" v-show="this.keyboardOption.show" @click="closeModal"></div>
    <div class="keyboard" v-show="showKeyboardFunc">
      <p v-for="keys in keyboardList">
        <template v-for="key in keys">
          <i v-if="key === 'del'" @click.stop="clickKey"  id="del"  class="bg-gray"><img @click.stop="clickKey"  id="del" src="../assets/backSpace.svg" class="backspace"></i>
          <i v-else-if="key === 'zero'" @click.stop="clickKey"  class=" tab-point zero">0</i>
          <i v-else-if="key === 'point'" @click.stop="clickKey"  class=" tab-point bg-gray dian">.</i>
          <i v-else-if="key === 'goBuy'" @click.stop="clickKey" id="goBuy" class="tab-point goBuy">确认并支付</i>
          <i v-else-if="key === '..'" class="tab-point diandain"></i>
          <i v-else @click.stop="clickKey" >{{key}}</i>
        </template>
      </p>
    </div>
  </div>
</template>

<script>
  import clickoutside from '../directives/clickoutside'
  export default {
    directives: { clickoutside },
    data() {
      return {
        keyboardList: [
          ['1', '2', '3' ,'del'],
          ['4', '5', '6' , ""],
          ['7', '8', '9',""],
          ['zero','point',".."],
          ["goBuy"]
        ]
      }
    },
    props: {
      keyboardOption : {  //获取父组件的参数
        type: Object
      },
      toPayFunc: {type: Function, default: ()=>{}},  //点击确认按钮事件
    },
    computed: {
      showKeyboardFunc(){
        return this.keyboardOption.show;
      }
    },
    methods: {
      //按钮点击事件
      clickKey(event) {

        let currentValStr = this.keyboardOption.value + "";  //当前金额
        let appendValue = event.srcElement.innerText; //需要新拼接的文本
        let id = event.srcElement.id;  //id
        let target = event.srcElement ? event.srcElement : event.target;

        if(id === 'del'){//如果点击的是id为del的表示是删除
          let valLength = currentValStr.length;
          if(valLength <= 0){
            return false;
          }
          this.keyboardOption.value = currentValStr.substring(0, valLength - 1 );
          return false;
        }else if(id === 'goBuy'){

          if(!this.keyboardOption.value){ //当金额没有时， 不隐藏
            return false;
          }

          this.keyboardOption.show=false;

          this.toPayFunc();
          return false;
        }

        //拼接字符串

        //仅支持1个小数点
        if(appendValue == '.' && currentValStr.indexOf(".") >= 0 ){
          return false;
        }

        if(currentValStr == "0" && appendValue != '.'){ //当前文本为0， 仅支持输入.
          return false;
        }

        //拼接字符串
        currentValStr =  currentValStr + "" + appendValue;

        //自动截取小数点后两位
        let dotIndex = currentValStr.indexOf(".");
        if( dotIndex >= 0 && (currentValStr.length - dotIndex) > 3 ){ //包含小数点 && 小数点后多于两位
          currentValStr = currentValStr.substring(0, dotIndex + 3);
        }

        //赋值
        this.keyboardOption.value = currentValStr;

      },
      closeModal(e) {
        if( (this.keyboardOption.value + "").endsWith(".")){  //避免出现 .结尾的数字
          this.keyboardOption.value = (this.keyboardOption.value + "").substring(0, (this.keyboardOption.value + "").length - 1);
        }

        this.keyboardOption.show = false;
        if(typeof (this.$parent.keyboardClose) == 'function'){ //调起父类的关闭事件
          this.$parent.keyboardClose();
        }
      }
    }
  }
</script>
<style scoped lang="less">

  .keyboard-mask {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    // background-color: rgba(0, 0, 0, 0.1);
    z-index: 998;
  }
   .backspace{
      width: 30%;
      margin-top: 8%;
   }

  .keyboard {
    display: inline-block;
    width: 100%;
    font-size: 18px;
    border-radius: 2px;
    background-color: #ffffff;
    user-select: none;
    position: absolute;/*定位数字键盘*/
    left: 0px;
    bottom: 0px;
    z-index: 999;
    pointer-events: auto;
    padding: 6px 6px 18px 0;
    p {
      width: 100%;
      margin: 0 auto;
      // height: 52px;
      // margin-bottom: 0.5em;
      display: flex;
      display: -webkit-box;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: center;
      i {
        display: block;
        // margin: 0 0.2%;
        width: 170px;
        height: 40px;
        margin: 0 0 6px 6px;
        line-height: 40px;
        font-style: normal;
        font-size: 18px;
        border-radius: 3px;
        background-color: #F7F7F7;
        text-align: center;
        flex-grow: 1;
        flex-shrink: 1;
        // flex-basis: 0;
        -webkit-box-flex: 1;

        &:active {
          // background-color: darken(#E6E6E6, 10%);
          background-color: #E6e6e6;
        }
      }
      .key-delete {
        width: 47px;
        margin: 0 0.2%;
        color: #0077aa;
        background:  #000;
      }
      .tab-num {
        font-size: 18px;
        background: #dedede;
        color: #5a5959;
      }
      .tab-point {
        width: 20px;
      }
      &:nth-child(2) {
        width: 100%;
      }
    }
  }
   // .bg-gray{
   //   background-color:#ececec !important;
   // }
   .goBuy{
     background-color: #07C160 !important;
     font-size: 14px !important;
     font-weight: 300;
     color: #ffffff;
     height: 132px !important;
     position: absolute;
     width: 23% !important;
     top:51px;
     right: 6px;
     line-height: 132px !important;
   }
   .goBuy:active{
     opacity: 0.9;
   }
   .zero{
     width: 50% !important;
   }
   .dian{
     width: 25% !important;
   }
   .diandain {
     width: 25% !important;
   }
</style>



